@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@400;500;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
/*********************************/
/*             General           */
/*===============================*/
html {
  @apply scroll-smooth;
}

body {
  @apply text-[15px] text-black;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  @apply font-libre_franklin;
}

p {
  @apply leading-relaxed;
}

/*********************************/
/*             Menu              */
/*===============================*/
.navbar {
  @apply fixed top-0 left-0 right-0 z-999 bg-transparent py-3 transition-all duration-500 font-libre_franklin;
}
.navbar .navigation {
  @apply w-full m-0;
}
.navbar .navigation .navbar-nav {
  @apply mt-4 flex flex-row overflow-y-scroll h-24;
}
.navbar .navigation .navbar-nav .nav-link {
  @apply block p-3 text-[12px] uppercase text-black tracking-wider hover:text-teal-500 dark:text-white/60 dark:hover:text-white font-medium;
}
.navbar .navigation .navbar-nav .active .nav-link {
  @apply text-teal-500 dark:text-white;
}
.navbar .navigation.ms-auto .navbar-nav li:last-child .nav-link {
  @apply pe-0;
}
.navbar .navigation.me-auto .navbar-nav li:first-child .nav-link {
  @apply ps-0;
}
.navbar .navbar-brand {
  @apply p-0;
}
.navbar .navbar-brand .l-dark,
.navbar .navbar-brand .logo-dark-mode {
  @apply hidden;
}
.navbar .navbar-brand .l-light,
.navbar .navbar-brand .logo-light-mode {
  @apply inline-block;
}
@media (max-width: 991.98px) {
  .navbar .navbar-brand .l-dark {
    @apply inline-block !important;
  }
  .navbar .navbar-brand .l-light {
    @apply hidden !important;
  }
}
.navbar .menu-social .login-btn-primary {
  @apply hidden;
}
.navbar .menu-social .login-btn-light {
  @apply inline-block;
}
@media (max-width: 991.98px) {
  .navbar .menu-social .login-btn-primary {
    @apply inline-block !important;
  }
  .navbar .menu-social .login-btn-light {
    @apply hidden !important;
  }
}
.navbar.is-sticky {
  @apply bg-white dark:bg-black shadow dark:shadow-gray-800;
}
.navbar.is-sticky .navbar-brand .l-dark {
  @apply inline-block;
}
.navbar.is-sticky .navbar-brand .l-light {
  @apply hidden;
}
.navbar.is-sticky .menu-social .login-btn-primary {
  @apply inline-block;
}
.navbar.is-sticky .menu-social .login-btn-light {
  @apply hidden;
}

@media (min-width: 992px) {
  .navbar {
    @apply py-3;
  }
  .navbar .navigation {
    @apply flex w-auto;
  }
  .navbar .navigation .navbar-nav {
    @apply h-auto mt-0 overflow-visible;
  }
  .navbar .navigation .navbar-nav .nav-link {
    @apply border-0 hover:bg-transparent;
  }
  .navbar .navigation .navbar-nav.nav-light .nav-link {
    @apply block p-3 text-white/60 hover:text-white;
  }
  .navbar .navigation .navbar-nav.nav-light .active .nav-link {
    @apply text-white;
  }
  .navbar .nav-icons .collapse-btn {
    @apply hidden;
  }
  .navbar.is-sticky {
    @apply py-3;
  }
  .navbar.is-sticky .navbar-nav .nav-link {
    @apply text-black dark:text-white/60;
  }
  .navbar.is-sticky .navbar-nav .active .nav-link {
    @apply text-teal-500;
  }
  .navbar.is-sticky .navbar-nav.nav-light .nav-link {
    @apply text-black hover:text-teal-500 dark:text-white/60 dark:hover:text-white;
  }
  .navbar.is-sticky .navbar-nav.nav-light .active .nav-link {
    @apply text-teal-500 dark:text-white;
  }
}
@media (max-width: 991px) {
  .navbar {
    @apply bg-white shadow dark:shadow-gray-800 dark:bg-black !important;
  }
  .navbar .navigation .navbar-nav {
    @apply block h-full max-h-80;
  }
  .navbar .navigation .navbar-nav .nav-link {
    @apply text-black;
  }
  .navbar .navigation .navbar-nav .active .nav-link {
    @apply text-teal-500;
  }
  .navbar .navigation .navbar-nav .menu-social {
    @apply mb-5 !important;
  }
}
/* // _footer.scss */
/*********************************/
/*         Footer                */
/*===============================*/
.footer .foot-subscribe input::placeholder {
  @apply text-gray-200;
}

/*********************************/
/*             Helper            */
/*===============================*/
/*********************************/
/*         Tobii Lightbox        */
/*===============================*/
/* Tobii Lighbox */
.tobii > button.tobii__close svg,
.tobii > button.tobii__prev svg,
.tobii > button.tobii__next svg {
  @apply h-9 w-auto;
}

.tobii__counter {
  @apply text-base;
}

.tobii-zoom {
  @apply block;
}

.tobii-zoom__icon {
  @apply hidden;
}

#grid {
  @apply p-0;
}

/*****************/
/*    Shapes     */
/*****************/
.shape {
  @apply absolute right-0 -bottom-px left-0;
}
.shape > svg {
  @apply w-full h-auto;
  transform: scale(2);
  transform-origin: theme("transformOrigin.top") theme("transformOrigin.center");
}

/*****************/
/*  back-to-top  */
/*****************/
.back-to-top {
  @apply hidden;
}

/*****************/
/*    Switcher   */
/*****************/
.label .ball {
  transition: transform 0.2s linear;
  @apply translate-x-0;
}

.checkbox:checked + .label .ball {
  @apply translate-x-6;
}

/*****************/
/*   Preloader   */
/*****************/
#preloader {
  background-image: linear-gradient(45deg, #ffffff, #ffffff);
  z-index: 99999;
  @apply fixed inset-0;
}
#preloader #status {
  @apply absolute left-0 right-0 top-1/2 -translate-y-1/2;
}
#preloader #status .spinner {
  @apply size-10 relative my-[100px] mx-auto;
}
#preloader #status .spinner .double-bounce1, #preloader #status .spinner .double-bounce2 {
  @apply w-full h-full rounded-full bg-teal-500/60 absolute top-0 left-0;
  animation: sk-bounce 2s infinite ease-in-out;
}
#preloader #status .spinner .double-bounce2 {
  animation-delay: -1s;
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
/* RTL */
html[dir=rtl] .tns-inner, html[dir=rtl] .tns-nav, html[dir=rtl] .slider, html[dir=rtl] .slider2, html[dir=rtl] .swiper-slider-hero {
  direction: ltr !important;
}
html[dir=rtl] .tns-visually-hidden {
  left: 0 !important;
  visibility: hidden;
}

/*****************/
/*  video iframe */
/*****************/
@media (max-width: 640px) {
  [data-type] iframe, [data-type] video {
    width: 500px;
    height: 300px;
  }
}
@media (max-width: 425px) {
  [data-type] iframe, [data-type] video {
    width: 360px;
    height: 260px;
  }
}
@media (max-width: 375px) {
  [data-type] iframe, [data-type] video {
    width: 100%;
    height: auto;
  }
}
.image-wrap {
  animation: 200s ppb_kenburns linear infinite alternate;
}

@keyframes ppb_kenburns {
  0% {
    transform: scale(1.3) translate(-10%, 10%);
  }
  25% {
    transform: scale(1) translate(0, 0);
  }
  50% {
    transform: scale(1.3) translate(10%, 10%);
  }
  75% {
    transform: scale(1) translate(0, 0);
  }
  100% {
    transform: scale(1.3) translate(-10%, 10%);
  }
}
/*****************/
/*  Swiper Slider     */
/*****************/
.swiper-slider-hero .swiper-container .swiper-button-prev:before,
.swiper-slider-hero .swiper-container .swiper-button-next:before {
  font-family: "Material Design Icons";
}
.swiper-slider-hero .swiper-container .swiper-button-prev:before {
  content: "󰅁";
}
.swiper-slider-hero .swiper-container .swiper-button-next:before {
  content: "󰅂";
}
.swiper-slider-hero .swiper-pagination-bullet {
  @apply text-gray-50 bg-transparent;
}
.swiper-slider-hero .swiper-pagination-bullet-active {
  @apply text-white;
}
.swiper-slider-hero .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  @apply my-0 mx-[13px];
}

/*********************************/
/*             Contact           */
/*===============================*/
/* Validation */
.error {
  @apply my-2 mx-0 hidden text-red-600;
}

#ajaxsuccess {
  @apply text-base w-full hidden clear-both my-2 mx-0;
}

.error_message,
#success_page {
  @apply p-2.5 mb-5 text-center rounded-md;
}

.error_message {
  @apply bg-red-600/5 text-red-600;
}

.contact-loader {
  @apply hidden;
}

#success_page {
  @apply bg-emerald-600/5 text-emerald-600;
}